.simple-table--cell{
  font-weight: bold;
  color: #276FC7;
}
.simple-table--cell:hover{
  color: #77AFF7;
  cursor: pointer;
  /* background-color: #F4F9FD; */
}

.simple-textarea-container{
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
  align-items: flex-start;
  flex-direction: column;
}

.simple-textarea-label{

  font-size: 0.5em;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}

.simple-textarea-content{
  width: 100%;
}



