/* Style the container */
.container {
  border-radius: 5px;
  /* background-color: #455d84; */
  /* color: #fff; */
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 5%;
  margin: 3%;
  margin-left: 10%;
  margin-right: 10%;
  
}

.container .section-title{
  font-size: 30px;
  padding-left: 10%;
  padding-right: 20%;
  margin: 0 5%;
}

/* Floating column for labels: 25% width */
.col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
  text-align: center;
}

/* Floating column for inputs: 75% width */
.col-75 {
  float: left;
  width: 75%;
  margin-top: 10px;
}

.row{
  width: 100%;
}

/* Clear floats after the columns */
.row:after {
  /* width: 100%; */
  /* content: ""; */
  /* display: table; */
  /* clear: both; */
  /* padding: 3%; */
}



/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .col-25,
  .col-75,
  button[class="isbar-save"] {
    width: 100%;
    margin-top: 0;
  }
  #go-back-button{
    font-size: 15px;
  }
}

